
*{
  font-family: 'Hiragino Mincho Pro';
}

.site-header{
  position:fixed;
  top:0;
  left:0;
  width:100%;
  z-index: 9999;
  height: 155px;
  border-bottom: 2px solid #1E1E1E;
  background-color: #fff;
}
.header-content{
  height: 100%;
}
.footer-content{
  background-color: #ececec;
}
.footer-content div{
  position: relative;
  height: 80px;
}
.footer-content div span{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  letter-spacing: 0.5em;
  text-align: center;
  font-size: 13px;
}
.site-name{
  display: inline-block;
  height: 100%;
  line-height: 1;
  .loopSlide{
    display: flex;
    width: 190vw;
    height: 100%;
    overflow: hidden;
  }

  img{
    width: 47%;
    height: 100%;
 
        &:first-child {
            animation: slide1 60s -10s linear infinite;
        }
 
        &:last-child {
            animation: slide1 60s linear infinite;
        }
  }
}
@keyframes slide1 {
  0% {
    transform: translateX(100%);
  }
  to {
    transform: translateX(-100%);
  }
}
 
@keyframes slide2 {
  0% {
    transform: translateX(0);
  }
  to {
    transform: translateX(-200%);
  }
}

.site-logo{
  position: absolute;
  right: 0;
  top: 0;
  width: 155px;
  height: 155px;
  border-left: 2px solid #1E1E1E;

  img{
    margin-left: 7%;
  }
}


@media screen and (max-width: 1024px) {
  .site-header{
    height: 55px;
    border-bottom: 1px solid #1E1E1E;
    background-color: #fff;
  }
  .site-logo{
    width: 55px;
    height: 55px;
    border-left: 1px solid #1E1E1E;
  }
  .footer-content div{
    height: 40px;
  }
  .footer-content div span{
    font-size: 5px;
  }
}